import * as React from 'react';
import { Navbar, NavbarHeading, NavbarGroup, NavbarDivider } from '@blueprintjs/core/';

export class NavBar extends React.Component<{title: string}, {}> {
    render() {
        return (
            <Navbar>
                <NavbarGroup>
                    <NavbarHeading>{this.props.title}</NavbarHeading>
                </NavbarGroup>
                <NavbarGroup>
                    {this.props.children}
                </NavbarGroup>
            </Navbar>
        );
    }
}


