import * as React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import { NavLink } from '../Navigation';

export class App extends React.Component<{}, {}> {
    render() {
        return (
            <Router>
                <div>{this.props.children}</div>
            </Router>
        );
    };
}

export class Area extends React.Component<{title: string, path: string, nav?: React.ReactType, exact?: boolean}, {}> {
    render() {
        return (
            <Route exact={this.props.exact || false} path={this.props.path} render={() => {
                return (
                    <div>
                        {this.props.nav && 
                            <this.props.nav title={this.props.title}>
                                {React.Children.map(this.props.children, (child, index) => {
                                    let page = child as any;
                                    return <NavLink title={page.props.title} path={page.props.path} />
                                })}
                            </this.props.nav>}
                        {this.props.children}
                    </div>
                );
            }} />
        );
    };
}

export class Page extends React.Component<{title: string, path: string, component?: React.ComponentClass, exact?: boolean}, {}> {
    render() {
        return (
            this.props.component? 
            <Route exact={this.props.exact || false} path={this.props.path} component={this.props.component} /> :
            <Route exact={this.props.exact || false} path={this.props.path} render={() => this.props.children}/>
        );
    };
}

export class Section extends React.Component<{}, {}> {
    render() {
        return (
            <div></div>
        );
    };
}

export class Layer extends React.Component<{}, {}> {
    render() {
        return (
            <div></div>
        );
    };
}




