import * as React from 'react';
import { NavLink as Link } from 'react-router-dom';

export class NavLink extends React.Component<{title: string, path: string, style?: any}, {}> {
    render() {
        return <Link to={this.props.path} activeStyle={this.props.style}>{this.props.title}</Link>
    }
}



